@import '@yandex-music-int/sol/theme/normalize.css';
@import '@yandex-music-int/sol/theme/theme.css';
@import '@yandex-music-int/sol/components/index.css';
@import '@yandex-music-int/sol/mixins/breakpoints.scss';

:root {
    --header-height: 72px;
    --player-bar-height: 80px;
    --player-bar-height-mobile: 66px;
    --gap-between-contents: 12px;
    --collection-page-header-height: 80px;
}

body {
    background-color: var(--ym-background-color-primary-enabled-basic);

    text-rendering: geometricprecision;

    color: var(--ym-controls-color-primary-text-enabled);

    font-family: var(--ym-font-text);
    font-size: var(--ym-font-size-label-m);
    font-weight: var(--ym-font-weight-normal);
    font-style: var(--ym-font-style-normal);
}

button {
    text-rendering: geometricprecision;
}

.desktop {
    --mobile-width: 770px;

    min-width: var(--mobile-width);
}

::selection {
    background-color: var(--ym-controls-color-primary-default-enabled);

    color: var(--ym-controls-color-primary-on_default-enabled);
}

.updateNotificationContainer {
    &_application {
        &_macos {
            inset-block-start: var(--ym-spacer-size-xxl);
        }

        &_windows {
            inset-block-start: var(--ym-spacer-size-xxxxl);
        }
    }

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        width: 100%;
    }
}
