.root {
    --title-bar-height: var(--ym-spacer-size-xxxl);
    --title-bar-button-width: 46px;
    --title-bar-close-button-color: #f23f42;

    display: flex;
    position: absolute;
    inset-block-start: 0;
    inset-inline-end: 0;
    inset-inline-start: 0;

    z-index: var(--ym-z-index-overlay);
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;

    width: 100%;
    height: var(--title-bar-height);
    user-select: none;
    -webkit-app-region: drag;

    overflow: hidden;
}

.button {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    margin: 0;
    outline: 0;
    border: 0;
    background: transparent;
    cursor: pointer;
    padding: 0;

    width: var(--title-bar-button-width);
    height: var(--title-bar-height);
    -webkit-app-region: no-drag;

    &:hover {
        background-color: var(--ym-controls-color-secondary-default-hovered);
    }
}

.closeButton {
    &:hover {
        background-color: var(--title-bar-close-button-color);
    }
}
